import React from "react";
import "./styles.css";
import menu from "./menu.json";
import Button from "@mui/material/Button";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import Fade from "@mui/material/Fade";

export default function App() {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <React.Fragment>
      <div>
        <Button
          id="fade-button"
          aria-controls={open ? "fade-menu" : undefined}
          aria-haspopup="true"
          aria-expanded={open ? "true" : undefined}
          onClick={handleClick}
        >
          MENU
        </Button>
        <Menu
          id="fade-menu"
          MenuListProps={{
            "aria-labelledby": "fade-button"
          }}
          anchorEl={anchorEl}
          open={open}
          onClose={handleClose}
          TransitionComponent={Fade}
        >
          <MenuItem onClick={handleClose}>{menu[0]["種類"]}</MenuItem>
          <MenuItem onClick={handleClose}>{menu[0]["menu"]}</MenuItem>
          <MenuItem onClick={handleClose}>{menu[0]["値段"]}</MenuItem>
          <MenuItem onClick={handleClose}>{menu[0]["写真"]}</MenuItem>
        </Menu>
      </div>

      {/* <ul>
        <li>{menu[0]["種類"]}</li>
        <li>{menu[0]["menu"]}</li>
        <li>{menu[0]["値段"]}</li>
        <li>{menu[0]["写真"]}</li>
      </ul>
      <ul>
        <li>{menu[1]["種類"]}</li>
        <li>{menu[1]["menu"]}</li>
        <li>{menu[1]["値段"]}</li>
        <li>{menu[1]["写真"]}</li>
      </ul> */}
    </React.Fragment>
  );
}
